body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(241, 241, 241);
}

.chart{
    height: 30px;
    color:aliceblue;
}
.chart img{
    height: 100%;
}
.addItem{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;   
}


.card {
    padding: 1rem;
    background: rgb(187, 210, 210);
    border-radius: 6px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 6px;
    overflow: hidden;
    margin: 0 auto;
}

.avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.itemList {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-around;
  }
  
  .greeting{
    font-weight: bolder;
    font-size: 3rem;
    color: rgb(163, 185, 222);
  }
p{
    width: 40px;
    display: inline-block;
}

p img{
    object-fit: contain;
    width: 90%;
    cursor: pointer;

}

.addtoCart{
    background-color: black;
    padding: 2px;   
    color: antiquewhite;
    cursor: pointer;
}

